import React from 'react';
import './LastMeasureResult.css';
import { connect } from 'react-redux';

const LastMeasureResult = ({ measurement }) => {
    
    const lastMeasure = measurement;
    console.log(lastMeasure);
    console.log(lastMeasure);

    return(
        <div className="last-measure-result">
            <p>Your last measure:</p>
            <h1>121/81</h1>
            <h1>Pulse - 86</h1>
            <p>05.01.2020 - 17:26</p>
        </div>
    );
}

const mapStateToProps = (state) => {
    return {
        measurement: state.firestore.ordered.measurement
    }
}
export default connect(mapStateToProps, null)(LastMeasureResult)